* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
  }
  
  .sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 78px;
    background: #4169E1;
    padding: 6px 14px;
    z-index: 1;
    transition: all 0.5s ease;
  
    &.open {
      width: 250px;
    }
  
    .logo-details {
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
  
      .logo_name {
        color: #fff;
        font-size: 20px;
        font-weight: 600;
        opacity: 0;
        transition: all 0.5s ease;
      }
  
      #btn {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        font-size: 22px;
        text-align: center;
        cursor: pointer;
      }
    }
  
    &.open {
      .logo-details,
      .logo-details .logo_name {
        opacity: 1;
      }
  
      .logo-details #btn {
        text-align: center;
      }
    }
  
    i {
      color: #fff;
      height: 60px;
      min-width: 50px;
      font-size: 28px;
      text-align: center;
      line-height: 60px;
    }
  
    .nav-list {
      margin-top: 20px;
      height: 100%;
    }
  
    li {
      position: relative;
      margin: 8px 0;
      list-style: none;
  
      input {
        font-size: 15px;
        color: #fff;
        font-weight: 400;
        outline: none;
        height: 50px;
        width: 100%;
        border: none;
        border-radius: 12px;
        transition: all 0.5s ease;
        background: #3629c0;
      }
  
      i {
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        border-radius: 12px;
      }
  
      a {
        display: flex;
        height: 100%;
        width: 100%;
        border-radius: 12px;
        align-items: center;
        text-decoration: none;
        transition: all 0.4s ease;
        background: #3629c0;
  
        &:hover {
          background: #fff;
  
          .links_name,
          i {
            transition: all 0.5s ease;
            color: #3629c0;
          }
        }
  
        .links_name {
          color: #fff;
          font-size: 15px;
          font-weight: 400;
          white-space: nowrap;
          opacity: 0;
          pointer-events: none;
          transition: 0.4s;
        }
      }
  
      .tooltip {
        position: absolute;
        top: -20px;
        left: calc(100% + 15px);
        background: #fff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
        padding: 6px 12px;
        border-radius: 4px;
        font-size: 15px;
        font-weight: 400;
        opacity: 0;
        white-space: nowrap;
        pointer-events: none;
        transition: 0s;
      }
  
      &:hover .tooltip {
        opacity: 1;
        pointer-events: auto;
        transition: all 0.4s ease;
        top: 50%;
        transform: translateY(-50%);
      }
  
      &.profile {
        position: fixed;
        height: 60px;
        width: 78px;
        left: 0;
        bottom: -8px;
        padding: 10px 14px;
        background: #3629c0;
        transition: all 0.5s ease;
        overflow: hidden;
  
        .profile-details {
          display: flex;
          align-items: center;
          flex-wrap: nowrap;
        }
  
        img {
          height: 45px;
          width: 45px;
          object-fit: contain;
          border-radius: 6px;
          margin-right: 10px;
        }
  
        .name,
        .job {
          font-size: 15px;
          font-weight: 400;
          color: #fff;
          white-space: nowrap;
        }
  
        .job {
          font-size: 12px;
        }
  
        #log_out {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          background: #1d1b31;
          width: 100%;
          height: 60px;
          line-height: 60px;
          transition: all 0.5s ease;
        }
      }
    }
  
    &.open {
      input {
        padding: 0 20px 0 50px;
        width: 100%;
      }
  
      li a .links_name {
        opacity: 1;
        pointer-events: auto;
      }
  
      li .tooltip {
        display: none;
      }
  
      li.profile {
        width: 250px;
      }
  
      .profile #log_out {
        width: 50px;
        background: none;
      }
    }
  
    .bx-search {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      font-size: 22px;
      background: #30269e;
      color: #fff;
  
      &:hover {
        background: #fff;
        color: #3629c0;
      }
    }
  
    &.open .bx-search:hover {
      background: #2a2096;
      color: #fff;
    }
  }
  
  .home-section {
    position: relative;
    background: #e4e9f7;
    min-height: 100vh;
    top: 0;
    left: 78px;
    width: calc(100% - 78px);
    transition: all 0.5s ease;
    padding: 20px; /* Ensures content is not cramped */
  
    .text {
      display: inline-block;
      color: #11101D;
      font-size: 25px;
      font-weight: 500;
      margin: 18px;
    }
  }
  
  .sidebar.open ~ .home-section {
    left: 250px;
    width: calc(100% - 250px);
  }